import { Component, OnInit } from '@angular/core';
import { AuthService } from '../services/auth.service'
import { ManageService } from '../services/manage/manage.service'
import { FlashMessagesService } from "angular2-flash-messages";
import { empty } from 'rxjs';

@Component({
  selector: 'app-manage',
  templateUrl: './manage.component.html',
  styleUrls: ['./manage.component.css']
})
export class ManageComponent implements OnInit {
  // Define
  newWord: String;
  tags: String[]=[];
  newTag:String;

  // end
  constructor(
    private auth:AuthService,
    private service:ManageService,
    private flashMsg:FlashMessagesService
  ) { }

  ngOnInit() {
    // this.keyword="fine";
  }
  addNewTag(){
    if(this.newTag=== undefined || this.newTag === null) {
      this.flashMsg.show('Tag cannot be blank', {cssClass:'alert-danger', timeout:3000});
      return false;
    }
    this.tags.push(this.newTag);
    // console.log(this.tags);
    
  }
  onAddWordSubmit() {
    // this.tags=["Nishad, Burhan"];
    const newWord = {
      word: this.newWord,
      tags: this.tags
    }
    // console.log(newWord);return;
    
    this.service.addNewWord(newWord).subscribe(data=>{
      if(data.success) {
        this.flashMsg.show('New Word Successfully Added', {cssClass:'alert-success', timeout:3000});
      } else {
        this.flashMsg.show('Error found while adding this word', {cssClass:'alert-danger', timeout:3000});
      }
    });
  }
}
