import { Component, OnInit } from '@angular/core';
import { ValidateService } from "../services/validate.service";
import { FlashMessagesService } from "angular2-flash-messages";
import { AuthService } from "../services/auth.service";
import { Router } from "@angular/router";
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  // Define
  name:String;
  username:String;
  email:String;
  password:String;
  // end

  constructor(
    private validateService:ValidateService, 
    private flashMsg:FlashMessagesService, 
    private auth:AuthService,
    private router:Router
    ) { }

  ngOnInit() {
  }

  onRegisterSubmit() {
    const newUser = {
      name:this.name,
      email:this.email,
      username:this.username,
      password:this.password
    };
    
    if(!this.validateService.validateRegister(newUser)) {
      this.flashMsg.show('Please fill all fields', {cssClass:'alert-danger', timeout:3000});
      return false;
    };

    if(!this.validateService.validateEmail(newUser.email)) {
      this.flashMsg.show('Email is not valid', {cssClass:'alert-danger', timeout:3000});
      return false;
    }

    this.auth.registerUser(newUser).subscribe(data => {
      if(data.success) {
        this.flashMsg.show('Registration Successful, You can login now!', {cssClass:'alert-success'});
        // this.router.navigate(['/login']);
        // this._cdr.detectChanges();
      } else {
        this.flashMsg.show('Registration Failed, Please try again!', {cssClass:'alert-danger', timeout:300});
      }
    });
  }
}
