import { Component, OnInit } from '@angular/core';
import { AuthService } from "../services/auth.service";
import { ManageService } from '../services/manage/manage.service'
import { FlashMessagesService } from "angular2-flash-messages";
import { Observable } from 'rxjs';
import { debounceTime, distinctUntilChanged, map } from 'rxjs/operators';
const wordsArry: string[] = [];
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  wordsArry: string;
  tags: string[] = [];
  public words: any;


  search = (text$: Observable<string>) =>
    text$.pipe(
      debounceTime(200),
      distinctUntilChanged(),
      map(term => term.length < 2 ? []
        : wordsArry.filter(v => v.toLowerCase().indexOf(term.toLowerCase()) > -1).slice(0, 10))
    )

  constructor(
    private auth: AuthService,
    private service: ManageService,
    private flashMsg: FlashMessagesService
  ) { 
    this.service.getAllWords().subscribe(words => {
      for(let word of words) {
        wordsArry.push(word.word);
      }
    });
  }

  ngOnInit() {
  }

  findMeaning() {
    const word = {
      word: this.wordsArry
    }
    // console.log(word);return;

    this.service.findWordByName(word).subscribe(data => {
      // this.tags=data.tags;
      // console.log(data.success);return;

      if (data.success) {
        this.tags = data.tags;
      } else {
        this.flashMsg.show(data.msg, { cssClass: 'alert-danger', timeout: 3000 });
      }

    })
  }

  // Search On Selected
  selectedItem(item) {
    const word = {
      word: item.item
    }
    // console.log(word);return;

    this.service.findWordByName(word).subscribe(data => {
      // this.tags=data.tags;
      // console.log(data.success);return;

      if (data.success) {
        this.tags = data.tags;
      } else {
        this.flashMsg.show(data.msg, { cssClass: 'alert-danger', timeout: 3000 });
      }

    })
  }

  copyToclipBoard(tag) {
    this.flashMsg.show("Copied to clipboard", { cssClass: 'alert-success', timeout: 4000 });
  }
}
